.preview {
  &__thumbs {
    margin-top: 10px;
  }
}

.preview-slider {
  border: 1px solid $border-color;

  &__frame {
    display: block;
    position: relative;
    z-index: 1;
    background-color: $white;

    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 75%;
    }

    img {
      width: 90%;
      height: 90%;
      object-fit: contain;
      object-position: 50% 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &--video {
      img {
        z-index: -1;
      }

      &::before {
        background-color: rgba(#d8e2eb, 0.5);
      }

      &::after {
        content: "";
        display: block;
        width: 40%;
        height: 40%;
        max-height: 80px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url("../images/icon-play-circle.png") center/contain
          no-repeat;
        z-index: 10;
      }
    }
  }
}

.thumb-slider {
  position: relative;

  &__frame {
    display: block;
    position: relative;
    z-index: 1;
    background-color: $white;
    border: 1px solid $border-color;
    border-radius: 4px;
    cursor: pointer;

    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 75%;
    }

    img {
      width: 90%;
      height: 90%;
      object-fit: contain;
      object-position: 50% 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &--video {
      img {
        z-index: -1;
      }

      &::before {
        background-color: rgba(#d8e2eb, 0.5);
      }

      &::after {
        content: "";
        display: block;
        width: 40%;
        height: 40%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url("../images/icon-play-circle.png") center/contain
          no-repeat;
        z-index: 10;
      }
    }
  }

  &.has-nav {
    padding: 0 24px;
  }

  &.has-nav &__prev,
  &.has-nav &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 100%;
    font-size: 20px;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;

    &:focus {
      outline: 0;
    }
  }

  &.has-nav &__next {
    left: auto;
    right: 0;
  }

  &:not(.has-nav) &__prev,
  &:not(.has-nav) &__next {
    display: none;
  }
}
